.App {
  text-align: center;
  background-color: cadetblue;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.header {
  display: grid;
  grid-template-columns: 90% 10%;
}

.cancel {
  /* width: 20%; */
  font-size: 3rem;
  border-radius: 15px;
  margin: 10px;
}

.textHeader {
  background-color: #A2DBFA;
  border-radius: 15px;
  margin: 10px;
  /* width: 180%; */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card-space {
  background-image: url("./Assets/card-background.jpeg");
}

.card-container {
  width: 100%;
  height: auto;
  margin: auto 0;
  display: grid ;
  grid-template-columns: auto auto auto auto;
  justify-content: center;
}

.card {
  padding: 5px;
  margin: 15px;
  width: 90%;
}
